@import './main.scss';

.navbar {
  @include flexbox;
  width: 86%;
  height: 8%;
  margin: 0 7%;
  border-top: 1px solid $deepgreen;

  button {
    font-family: $serif;
    color: $deepgreen;
    font-size: 13px;
    margin: 0 25px;
    transition: font-size 200ms ease-in-out;

    &.isActive {
      font-size: 19px;
    }
  }

  button:hover {
    font-size: 18px;
  }

  @include responsive(T) {
    button {
      font-size: 16px;
      margin: 0 40px;

      &.isActive {
        font-size: 22px;
      }
    }

    button:hover {
      font-size: 22px;
    }
  }
}
