@import './main.scss';

.profile {
  width: 90%;
  max-width: 600px;
  height: 90%;
  padding: 20px;
  background-color: $white;
  color: $deepgreen;
  overflow-y: scroll;

  .info {
    padding: 15px 0;

    .title {
      display: inline-block;
      font-family: $bold;
      color: $deepgreen;
      padding: 10px;
      font-size: 16px;
      padding: 1px 0;
    }

    p {
      font-size: 13px;
      font-family: $medium;
      color: $primary;
    }

    .emphasize {
      font-family: $medium;
      color: $deepgreen;
      margin-right: 5px;
    }

    img {
      display: block;
      width: 200px;
    }
  }

  @include responsive(T) {
    margin: 20px 5%;

    .info {
      .title {
        font-size: 19px;
      }

      p {
        font-size: 14px;
      }

      img {
        width: 230px;
      }
    }
  }
}

.profile::-webkit-scrollbar {
  display: none;
}
