@import './main.scss';

.modal {
  position: absolute;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100vw;
  height: 80vh;
  overflow-y: scroll;
  background-color: rgba(0, 0, 0, 0.9);
  color: $primary;
  text-align: center;
  transform: translate3d(200%, 100vh, 0) scale(1, 0.1);
  transition: transform 500ms ease-in-out;

  &.isActive {
    transform: translate3d(200%, 0, 0) scale(1, 1);
  }

  .workDetail {
    width: 90%;
    max-width: 1000px;
    margin: 50px auto;
    padding: 30px;
    background: $white;

    .title {
      font-family: $bold;
      font-size: 22px;
      color: $deepgreen;
      text-align: center;
    }

    .yearAndGenre {
      font-family: $medium;
      display: block;
      color: $deepgreen;
      font-size: 12px;
      text-align: center;
      margin-bottom: 10px;

      .year,
      .genre {
        font-family: $medium;
        display: inline;
      }
    }

    .tool {
      font-family: $medium;
      color: $deepgreen;
      font-size: 12px;
      text-align: center;
      margin-bottom: 30px;
    }

    .skillTitle {
      font-family: $bold;
      color: $primary;
      font-size: 14px;
    }

    .skillList {
      list-style: inside;
      text-align: left;
      background-color: rgba(62, 133, 2, 0.4);
      border-radius: 10px;
      padding: 15px;

      .skill {
        font-size: 14px;
        margin: 10px 0;
      }
    }

    .detail {
      font-size: 14px;
      color: $primary;
      margin: 15px 0;
    }

    .detailImg,
    .detailVideo {
      display: block;
      width: 90%;
      max-width: 500px;
      margin: 3px auto;
    }

    @include responsive(T) {
      .title {
        font-size: 28px;
      }

      .detailImg {
        width: 70%;
        margin: 3px auto;
      }
    }
  }

  //ARTWORK//

  .artWorkImg {
    width: 60%;
    min-width: 200px;
    max-width: 500px;
    margin: 10px auto;
  }

  .modal ::-webkit-scrollbar {
    display: none;
  }
}
